<!--Component de la pagina de formulari  on qualsevol persona pot escriure el seu record -->
<template> 
  <div> 
    <b-form-textarea
      id="textarea"
      v-model="text"
      placeholder="Escriu aquí el teu record..."
      rows="3"
      max-rows="6"
    ></b-form-textarea>
    <pre class="mt-3 mb-0">{{ text }}</pre>
  </div>
</template>

<script> 
  export default {
    data() {
      return {
        text: ''
      }
    }
  }
</script>

<style>
  .form-control{
    margin-bottom: 1em;
  }
</style>