<!--Aquesta es la pagina on s'emmagatzemen tots els records. Al template cridem els components de les cards i la navegacio dels records en ordre -->
<template>
  <div class="analisi">
    <h2 class="pb-2">
      Anàlisis de somnis
    </h2> 
      <div class="container-md">
        <b-card-group deck>
          <div columns v-for="proyecto in proyectos" :key="proyecto.id">
            <Card2 :autor="proyecto.autor" :srcimg="proyecto.srcimg" :alt="proyecto.alt" :titulo="proyecto.titulo" :texto="proyecto.texto"></Card2> 
          </div>
        </b-card-group>
        <div>
          <NavPages></NavPages>
        </div>
      </div>
  </div>

</template>

<script>
  import Card2 from '../components/CardTwo.vue';
  import NavPages from '../components/NavPages.vue';
  const lstProysJson = '../Algo.json';

  export default {
    props: {  
    name: {
        type: String,
        default: 'Algo.json'
      }
    },
    components: {
      Card2,
      NavPages
    },
    data() {
      return {
        proyectos:[],
        githubPage: 1,
        page: 1,
        loading: false,
        perPage: 5
      }
    },
    mounted() {
      this.fetchData()
    },
    methods: {
      async fetchData() {
        const res = await fetch (lstProysJson);
        const val= await res.json();
        this.proyectos=val;
        console.log(val);
      }
    }
  }
</script>

