<!--Aquest és el component de l'estructura de les cards -->
<template>
  <div>
    <b-card
      :title="titulo"
      :img-src="srcimg"
      :img-alt="alt"
      img-top
      :footer="autor"
      tag="proyecto"
      style="max-width: 20rem;"
      class="mb-2"
    >
      <b-card-text>
        {{texto}}
      </b-card-text>
    </b-card>   
  </div>
</template>
<!--A l'script exportem a l'arxiu json els elements que tindra cada card -->  
<script>
  export default {
    props: ["id", "autor", "titulo", "srcimg", "alt", "texto"]
  };
</script>
 
<style>
  b-card{
    background-color:#C5D8FA
  }
</style>